<template>
  <section class="page-section" id="contact">
    <div class="container">
      <div class="row">
        <div class="col-lg-12 text-center">
          <h2 class="section-heading text-uppercase">Contact Us</h2>
          <h3 class="section-subheading text-muted">If you have a query or want to say something, Please feel free to drop a message.</h3>
        </div>
      </div>
      

    
      <div class="row">
        <div class="col-lg-12">
          <form id="contact" v-on:submit="processForm"  novalidate="novalidate">
            <div class="row">
              <div class="col-md-6">
                <div class="form-group">
                  <input class="form-control" id="name" v-model="username" type="text" placeholder="Your Name *" required="required" data-validation-required-message="Please enter your name.">
                  <p class="help-block text-danger"></p>
                </div>
                <div class="form-group">
                  <input class="form-control" id="email" v-model="email" type="text" placeholder="Your Email *" required="required" data-validation-required-message="Please enter your email address.">
                  <p class="help-block text-danger"></p>
                </div>
                <div class="form-group">
                  <input class="form-control" id="phone" v-model="phone" type="text" placeholder="Your Phone *" required="required" data-validation-required-message="Please enter your phone number.">
                  <p class="help-block text-danger"></p>
                </div>
              </div>
              <div class="col-md-6">
                <div class="form-group">
                  <textarea class="form-control" id="message"  v-model="message" type="text"  placeholder="Your Message *" required="required" data-validation-required-message="Please enter a message."></textarea>
                  <p class="help-block text-danger"></p>
                </div>
              </div>

              <div class="clearfix"></div>
              <div class="col-lg-12 text-center">
                <div id="success"></div>
                
                <vue-loaders  v-if="loader" class="loader" name="ball-scale-multiple" color="white" scale="2"></vue-loaders>
                <button  v-if="button" class="btn btn-primary btn-xl text-uppercase fixsized" type="submit">Send Message</button>
                  <div class='success'  v-if="sent" >{{success}}</div> 
              </div>
              
            </div>
          </form>
        </div>
      </div>
    </div>
  </section>

</template>

<script>
const axios = require('axios');
export default {
  data() { 
    return {
        button: true,
        loader: false,
        sent: false,
        success: "Thanks Got your response, I will get back to you, ASAP! ",
        email: "",
        username: "",
        phone: "",
        message: "",
        extra: ""
    };
  },
  methods: {
     processForm: function() {
       this.button = false;
       this.loader = true;
       
       axios({
        method: 'post',
        url: 'https://nodejs-mail-sender.herokuapp.com/send_email',
        headers: { 'Content-Type' : 'application/json' }, 
        data: {
            email: this.email,
            message: this.message,
            phone: this.phone,
            name: this.username,
        }
      })
      .then((res)=>{
        this.sent = true; 
        this.button = true;
        this.loader = false;
        this.extra = res
      })
  },
  }
}
</script>

<style scoped>
.success {
  padding-top: 30px;
  color: rgb(51, 175, 51)
}
#sendMessageButton:hover {
  color:black;
}
#sendMessageButton:active {
  color:white;
}
</style>